import React from 'react';
import './css/cont3.css';
import img from './images/admin.jpg';

const Cont3 = () => {
    return(
        <div className="cont3">
            <div className="c-li-1">
                <div id = "skills" className="box">
                    <div className="headline-l">
                        <line></line>
                        <p className="headline-t">WebDesign</p>
                    </div>
                    <div className="textline">
                        As a WebDeveloper mostly i go for 
                        the look and feel of the Webpage, So that the
                        clients or persons who visit the page will
                        be satisfied.My main opinion and moto is to Maintain
                        the Simplicity on every Webpage i make. 
                    </div>
                </div>
                <div className="box">
                    <div className="headline-l">
                        <line></line>
                        <p className="headline-t">Pentester</p>
                    </div>
                    <div className="textline">
                        Pentesting is my Hobby. It gives a satisfying 
                        feel when working or learing on new concepts.
                        Most of the time ill spend for learing and teaching 
                        others about this field.
                    </div>
                </div>
                <div className="box">
                    <div className="headline-l">
                        <line className="line3"></line>
                        <p className="headline-t">Photography</p>
                    </div>
                    <div className="textline">
                        Photography is my Passion, Simply its my LIFE.
                        I create good concept photos.My photography is like a 
                        brain to the body and my Editography is like
                        a heart which brings life to the photos.My creative
                        skills and hard work always resulted in positive
                        results and made satisfactory client base.  
                    </div>
                </div>
            </div>
            <div id = "about" className="c-li-2">
                <div className="image-bg">
                    <img className="image" src = {img}></img>
                </div>
                <div className="text-box">
                    <div className="headline-l">
                        <line className="line4"></line>
                        <p className="headline-t1">About Me</p>
                    </div>
                    <p className="headline-t2">Honey Hazrath</p>
                    <div className="textline2">
                        Hi in am Honey. Iam a Photoshop artist,
                        Pentester, and WebDeveloper. Mostly i do All 
                        My work on Android coz it makes some of my work 
                        easy and clean.
                        So Before leaving my Porfolio visit my photo 
                        Collection by clicking the below button.<br/>
                        Learn Teach and enjoy.Hope you have a great day.
                    </div>
                    <div className="btn3-bg">
                        <button className="btn3"><a href="#">Creations</a></button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Cont3;