import React from 'react';
import './css/navbar.css';
import './css/resp.css';

var Navbar = () => {

    var openSideMenu = () => {
        document.getElementById('hammenu').style.width
        = '250px';
    }
    var closeSideMenu = () => {
        document.getElementById('hammenu').style.width
        = '0px';
    }

    return(
    <div>
        <div className="navbar">
            <div className="hamenu" onClick = {openSideMenu}>
                <svg width = "30" height = "26">
                    <path d = "M0,5 30,5" stroke = "#fff"
                    strokeWidth = "5" />
                    <path d = "M0,14 30,14" stroke = "#fff"
                    strokeWidth = "5" />
                    <path d = "M0,23 30,23" stroke = "#fff"
                    strokeWidth = "5" />
                </svg>
            </div>
            <div className="title">
                HONEY
            </div>
            <div className="navlist">
                <ul>
                    <li className="list-comp"><a href="#home" className="link">Home</a></li>
                    <li className="list-comp"><a href="#about" className="link">About</a></li>
                    <li className="list-comp"><a href="#skills" className="link">Skills</a></li>
                    <li className="list-comp"><a href="https://instagram.com/honey_hazrath127" className="link">Mail Me</a></li>
                    {/* <li className="list-comp"><a href="#" className="link">FAQ</a></li> */}
                    {/* <li className="list-comp"><a href="#" className="link">Contact</a></li> */}
                </ul>
            </div>
            <div className="btn-box">
                <button className="btn"><a className="link link-b" href="https://instagram.com/honey_hazrath127">HIRE ME</a></button>
            </div>
        </div>
        <div id="hammenu" className="ham-navlist">
            <ul>
                <li className="list-side slideclose" onClick = {closeSideMenu}>X</li>
                <li className="list-side" onClick = {closeSideMenu}><a href="#home" className="link">Home</a></li>
                <li className="list-side" onClick = {closeSideMenu}><a href="#about" className="link">About</a></li>
                <li className="list-side" onClick = {closeSideMenu}><a href="#skills" className="link">Skills</a></li>
                <li className="list-side" onClick = {closeSideMenu}><a href="https://instagram.com/honey_hazrath127" className="link">Mail Me</a></li>
                {/* <li className="list-side" onClick = {closeSideMenu}><a href="#" className="link">FAQ</a></li> */}
                {/* <li className="list-side" onClick = {closeSideMenu}><a href="#" className="link">Contact</a></li> */}
            </ul>
            <ul className="social-icon">
                    <li className="ls"><a href="#" className="link"><i className="fa fa-facebook-f"></i></a></li>
                    <li className="ls"><a href="https://instagram.com/honey_hazrath127" className="link"><i className="fa fa-instagram"></i></a></li>
                    <li className="ls"><a href="#" className="link"><i className="fa fa-twitter"></i></a></li>
                    <li className="ls"><a href="#" className="link"><i className="fa fa-github"></i></a></li>
            </ul>
        </div>
    </div>
    );
}
export default Navbar;